*{
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Poppins",sans-serif;

}

body{
  background-color: #dbe4ee;
}
.timer{
  width: 80vw;
  display: flex;
  justify-content: space-around;

  gap: 10px;
  position: absolute;
  transform: translate(-50%,-50%);
  top: 50%;
  left: 50%;
}

.field{
  width: 28vmin;
  height: 28vmin;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  position: relative;
  box-shadow: 15px 15px 30px rgba(0,0,0,0.5);
  font-size: 12px;
    color: #ffffff;
}
.value{
  background-color: #ffffff;
  height: 100%;
  color: #032d4d;
  width: 100%;
  display: grid;
  place-items: center;
  font-weight: 600;
  font-size: 5em;
}

.label{
  font-size: 1.4em;
  background-color: #03478a;
  display: block;
  width: 100%;
  text-align: center;
  padding: 0.1em 0;
  font-weight: 400;
}
input{
  border:none; 
   outline:none;
  display:inline-block;
  height:50px;
  margin-top: 28px;
  vertical-align:middle;
  border-radius:22px;
  width:220px;
  
  padding:0 20px; 
  position:relative;
  left: 50%;
  transform: translate(-50%,-50%);
    }

   button{ 
    color:#fff;
    /* left:15px;*/
    top:25px;  
    height:50px;
    width:120px;
    cursor:pointer;
    border-radius:22px;
    padding:10px 25px; 
    background-color: #03478a;
    font-size: 1.4em;
    position:absolute;
   transform: translate(-50%,-50%);
  top: 20%;
  left: 50%;
  } 



